import React from 'react'
import './SignUp.css';

function FormSignUp(props) {
    return (
        <form onSubmit={props.Submit3}>
          <div className="inBox1">
            <h2 className="H_f2">SIGN UP</h2>
            <label htmlFor="in01" className="l1">FULL NAME
              <input type="text" name="in01" className="SignInp1" value={props.SiUpValue1} onChange={props.handleChangeSiUp1}/>
            </label>
            <label htmlFor="in02" className="l1">YOUR EMAIL
              <input type="email" name="in02" className="SignInp1" value={props.SiUpValue2} onChange={props.handleChangeSiUp2}/>
            </label>
            <label htmlFor="in03" className="l1" >PASSWORD
              <input type="password" name="in03" className="SignInp1" value={props.SiUpValue3} onChange={props.handleChangeSiUp3}/>
            </label>
          </div>
        <div className="d1">YOUR GENDER</div>
        <div className="box_radio">
          <label htmlFor="r1" className="labelR1">
            <input type="radio" name="r1" className="radio1" checked={props.checked2} onChange={props.handleChangeSiUp4}/>Male
          </label>
          <label htmlFor="r1"className="labelR1" >
            <input type="radio" name="r1" className="radio1" checked={props.checked3} onChange={props.handleChangeSiUp5}/>Female
          </label>
        </div>
        <label htmlFor="textAr" className="LtextAr">BIO</label>
        <textarea  name="textAr" className="TextAreaSiUp" value={props.SiUpValue4} onChange={props.handleChangeSiUp6}/>
        
        <input type="submit" value="Sign up" className="Sbutton11"/>
        <input type="reset" value="Cancel" className="Sbutton22"/>
      </form>
    )
}
export default FormSignUp;