import React, { Component } from 'react'

class Conditional extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             isLoggedIn:true
        }
    }
    
    render() {
        if (this.state.isLoggedIn){
            return (
                <div>
                    Hi, User!
                </div>
            )
        }else{
            return (
                <div>
                    Hi, Gest!
                </div>
            )
        }

       
    }
}

export default Conditional
