
import React,{Component} from 'react'
import './ParentStyle.css';
import FormSignIn from './FormSignIn'
import FormSignUp from './FormSignUp'
import FormRecover from './FormRecover'

export class FormParent extends Component {
    constructor(props) {
      super(props);
      
        this.state = {
          value: '',
          value2: '',
          value3: '',
          value4: '',
          value5: '',
          ch01: false,
          value6: '',
          value7: '',
          value8: '',
          value9: '',
          ch02: '',
          ch03: '',
          value10: '',

      };
        
        this.handleChange = this.handleChange.bind(this);
        this.handleChange2 = this.handleChange2.bind(this);
        this.handleChange3 = this.handleChange3.bind(this);
        this.handleChange4 = this.handleChange4.bind(this);
        this.handleChange5 = this.handleChange5.bind(this);
        this.handleChange6 = this.handleChange6.bind(this);
        this.handleChange7 = this.handleChange7.bind(this);
        this.handleChange8 = this.handleChange8.bind(this);
        this.handleChange9 = this.handleChange9.bind(this);
        this.handleChange10 = this.handleChange10.bind(this);
        this.handleChange11 = this.handleChange11.bind(this);
        this.handleChange12 = this.handleChange12.bind(this);
        this.handleChange13 = this.handleChange13.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleSubmit2 = this.handleSubmit2.bind(this);
        this.handleSubmit3 = this.handleSubmit3.bind(this);
        this.handleSubmit4 = this.handleSubmit4.bind(this);
    }
    handleChange(event) {
      this.setState({
        value: event.target.value
      }); 
    }
    handleChange2(event) {
      this.setState({
        value2: event.target.value
      }); 
    }
    handleChange3(event) {
      this.setState({
        value3: event.target.value
      }); 
    }
    handleSubmit(event) {
      this.setState({
        message1: this.state.value,
        message2: this.state.value2,
        message3: this.state.value3
      });
      console.log(this.state.value2);
      event.preventDefault();
    }
    /*----------------------------------------------------*/ 
    handleChange4(event) {
      this.setState({
        value4: event.target.value,
      }); 
    }
    handleChange5(event) {
      this.setState({
        value5: event.target.value
      }); 
    }
    handleChange6(event) {
      this.setState({
        ch01: event.target.checked
      }); 
    }

    handleSubmit2(event) {
      this.setState({
        message4: this.state.value4,
        message5: this.state.value5,
        ch01: this.state.ch01===true? this.state.ch01="YES" : this.state.ch01=false
      });
      console.log(this.state.value2);
      event.preventDefault();
    }

    /*-------------------------------------------------------------------------------------*/
    handleChange7(event) {
      this.setState({
        value6: event.target.value
      }); 
    }
    handleSubmit3(event) {
      this.setState({
        message6: this.state.value6
      });
      event.preventDefault();
    }
    /*-------------------------------------------------------------------------------------*/
    handleChange8(event) {
      this.setState({
        value7: event.target.value
      }); 
    }
    handleChange9(event) {
      this.setState({
        value8: event.target.value
      }); 
    }
    handleChange10(event) {
      this.setState({
        value9: event.target.value
      }); 
    }
    handleChange11(event) {
      this.setState({
        ch02: event.target.checked
      }); 
    }
    handleChange12(event) {
      this.setState({
        ch03: event.target.checked
      }); 
    }
    handleChange13(event) {
      this.setState({
        value10: event.target.value
      }); 
    }
    handleSubmit4(event) {
      this.setState({
        message7: this.state.value7,
        message8: this.state.value8,
        message9: this.state.value9,
        message10: this.state.ch02===true? this.state.ch02="YES" :  this.state.ch02=false,
        message11: this.state.ch03===true? this.state.ch03="YES" : this.state.ch03=false,
        message12: this.state.value10
      });
      event.preventDefault();
    }

    render(){
        
      return (
    <div className="wrapper">    
    <div className="H_block">
      <div className="twoForm">
        <FormSignIn Submit1={this.handleSubmit2}
         SiInValue1={this.state.value4}
          handleChangeSiIn1={this.handleChange4}
          SiInValue2={this.state.value5}
          handleChangeSiIn2={this.handleChange5}
          checked1={this.state.ch01}
          handleChangeSiIn3={this.handleChange6}
           />
        <FormRecover Submit2={this.handleSubmit3}
          RecValue1={this.state.value6}
          handleChangeRec1={this.handleChange7}
        />
      </div>  
        <FormSignUp Submit3={this.handleSubmit4}
          SiUpValue1={this.state.value7}
          handleChangeSiUp1={this.handleChange8}
          SiUpValue2={this.state.value8}
          handleChangeSiUp2={this.handleChange9}
          SiUpValue3={this.state.value9}
          handleChangeSiUp3={this.handleChange10}
          checked2={this.state.ch02}
          handleChangeSiUp4={this.handleChange11}
          checked3={this.state.ch03}
          handleChangeSiUp5={this.handleChange12}
          SiUpValue4={this.state.value10}
          handleChangeSiUp6={this.handleChange13}
        />
        
        <form className="ContForm" onSubmit={this.handleSubmit}>
            <h1 className="H_contact">CONTACT FORM</h1>
            <div className="Cont_In_box1">
              <div className="InBlock">
                <label className="Cont_l1" htmlFor="C_in1">YOUR EMAIL
                <input type="email" className="Cont_in1" name="C_in1" value={this.state.value} onChange={this.handleChange}/>
                </label>
              </div>
              <div className="InBlock">  
                <label className="Cont_l2" htmlFor="SelectIn">SUBJECT</label>
                  <select name="SelectIn" className="Sel" value={this.state.value2} onChange={this.handleChange2}>
                      <option value="Select subject">Select subject</option>
                      <option value="Select subject2">subject2</option>
                      <option value="Select subject3">subject3</option>
                      <option value="Select subject4">subject4</option>
                  </select>
              </div>
            </div>
            <div className="InBlock2">
              <label htmlFor="TextArL2" className="Cont_l2">Message</label>
                <textarea name="TextArL2" className="TextAr2" value={this.state.value3} onChange={this.handleChange3}/>
            </div>
            <input type="submit" value="SEND" className="ButtonCont1"  />
            <input type="reset" value="Cancel" className="ButtonCont2"/>
          </form>
        </div>
        <div className="Rezultat">
          <h2>CONTACT FORM</h2>
          <p className="RezP">EMAIL: {this.state.message1}</p>
          <p className="RezP">SUBJECT: {this.state.message2}</p>
          <p className="RezP">MESSAGE: {this.state.message3}</p>
          <div className="Line"></div>
          <h2>SIGN IN</h2>
          <p className="RezP">USERNAME: {this.state.message4}</p>
          <p className="RezP">PASSWORD: {this.state.message5}</p>
          <p className="RezP">Remember me: {this.state.ch01}</p>
          <div className="Line"></div>
          <h2>RECOVER PASSWORD</h2>
          <p className="RezP">YOUR EMAIL: {this.state.message6}</p>
          <div className="Line"></div>
          <h2>SIGN UP</h2>
          <p className="RezP">FULL NAME: {this.state.message7}</p>
          <p className="RezP">YOUR EMAIL: {this.state.message8}</p>
          <p className="RezP">PASSWORD: {this.state.message9}</p>
          <p className="RezP">Male: {this.state.message10}</p>
          <p className="RezP">Female: {this.state.message11}</p>
          <p className="RezP">BIO: {this.state.message12}</p>
        </div>
      </div> 
      )
    }
  }
  export default FormParent; 