import React from 'react'
import './SignIn.css';
function FormSignIn(props) {
    return (
        <form onSubmit={props.Submit1}>
          <h2 className="H_f1">SIGN IN</h2>
          <label htmlFor="in1" className="l1" >USERNAME
            <input type="text" className="SignInp1" name="in1" placeholder="exemple" value={props.SiInValue1} onChange={props.handleChangeSiIn1}/>
          </label>
          <label htmlFor="in2" className="l1">PASSWORD
            <input type="password" className="SignInp1" name="in2" value={props.SiInValue2} onChange={props.handleChangeSiIn2}/>
          </label>
          <input type="checkbox" name="in3" className="ch1" checked={props.checked1} onChange={props.handleChangeSiIn3}/><label htmlFor="in3" className="l2">Remember me</label>
          <input type="submit" value="Sign in" className="Sbutton1"/>
          <input type="button" value="Forgot password" className="Sbutton2"/>
      </form>
    )
}
export default FormSignIn;