import React from 'react'
import './RecoverStyle.css';
function FormRecover(props) {
    return (
        <form onSubmit={props.Submit2}>
            <h2 className="H_R1">RECOVER PASSWORD</h2>
        <label htmlFor="emailRecover" className="lr1">YOUR EMAIL
          <input type="email" name="emailRecover" className="inR1" value={props.RecValue1} onChange={props.handleChangeRec1}/>
        </label>
        
        <input type="submit" value="Send" className="buttonR1"/>
        <input type="submit" value="Sign up" className="buttonR2"/>
      </form>
    )
}
export default FormRecover;